<template>
  <section class="send-link">
    <b-tabs nav-wrapper-class="bg-dark container text-uppercase">
      <b-tab title="На почту" active>
        <b-container>
          <c-row-item-block>
            <p class="no-margin">
              Ваш партнер Пиццерия в Люблино
              получит ссылку для оплаты выбранного
              тарифного плана.
            </p>
          </c-row-item-block>
          <b-form @submit.prevent="submitForm">
            <b-form-group id="fieldset1"
                          label-for="tariff"
                          :state="undefined"
                          required>
              <b-form-select
                      v-model="form.tariff"
                      :options="tariffsList"
                      v-select-placeholder/>
            </b-form-group>
            <b-form-group id="fieldset2"
                          label-for="email"
                          :state="undefined">
              <b-form-input id="email"
                            placeholder="Электронная почта"
                            required
                            type="email"
                            v-model.trim="form.email"></b-form-input>
            </b-form-group>
            <b-button variant="primary" type="submit">Отправить ссылку</b-button>
          </b-form>
        </b-container>
      </b-tab>
      <b-tab title="Смс">
        <!-- Content -->
      </b-tab>
    </b-tabs>
  </section>
</template>

<script>
import modelHelper from '../../../mixins/FetchHelper.js';

export default {
  name: 'send-link',
  mixins: [modelHelper],
  data () {
    return {
      form: {
        tariff: null,
        email: ''
      },
      requestUrl: '/api/post-success',
      fetchUrl: '/api/partners/new',
      isLoadDisabled: false,
      fetchedData: {},
    }
  },
  methods: {
    submitForm() {
      let url = this.requestUrl;
      this.axios.post(url, this.form)
        .then(response => {
          this.$router.back();
        })
        .catch(e => {
          this.errors.push(e);
        });
    }
  },
  computed: {
    tariffsList() {
      let list = this.fetchedData.tariffs || [];

      list.unshift({
        value: null,
        text: 'Выберите тарифный план'
      });

      return list;
    }
  },
  mounted() {

  }
}
</script>

<style lang="scss">
  .send-link {
    .nav-tabs {
      max-width: 767px;
    }
  }
</style>


