<template>
  <section class="teams pb-5">
    <div class="teams__header bg-dark text-inverse pb-3">
      <b-container>
        <small>Мои счета</small>
        <div class="d-flex text-center mt-1 mb-3">
          <div class="teams__account-item h2 mb-0" @click="showRoubleAccountModal">
            <span v-price-format="fetchedData.totalRub"></span> ₽
          </div>
          <div class="teams__account-item h2 mb-0" @click="showBncAccountModal">
            <span v-price-format="fetchedData.totalBnc"></span> BnC
          </div>
        </div>
        <div class="d-flex align-items-center justify-content-end">
          <div class="d-flex align-items-center text-right">
            <small class="text-muted mb-0 mr-3">
              уровень <br>
              партнерства
            </small>
            <div class="h1 m-0 text-primary">{{ fetchedData.level }}</div>
          </div>
          <div class="ml-3">
            <b-button variant="primary" type="submit">Отправить ссылку</b-button>
          </div>
        </div>
      </b-container>
    </div>
    <section>
      <b-container>
        <div class="row-item-flex bordered-bottom">
          <div>
            <div><small class="text-uppercase text-muted text-muted">мои субфранчайзи · <span>{{ fetchedData.list.length }}</span></small></div>
            <span class="afake text-info" @click="expandItems">
              <span v-if="!expandAll">показать всех</span>
              <span v-if="expandAll">свернуть всех</span>
            </span>
          </div>
          <div class="text-right">
            <small class="text-muted"><span v-price-format="getTotalIncome(fetchedData.list)"></span> ₽</small>
            <b-form-group id="fieldset1"
                          label-for="select"
                          class="mb-0"
                          :state="undefined">
              <b-form-select
                      class="bg-transparent small text-uppercase teams__interval"
                      v-model="fetchedData.defaultInterval"
                      @change="changeInterval"
                      :options="fetchedData.intervals"
                      v-select-width/>
            </b-form-group>
          </div>
        </div>
      </b-container>
    </section>
    <section v-if="fetchedData.list && fetchedData.list.length">
      <section class="bg-white bordered-bottom" v-for="(item, index) in fetchedData.list" :class="{'mb-2': (item.expanded && index !== fetchedData.list.length - 1), 'mt-2': (item.expanded && index !== 0) }">
        <b-container>
          <div class="row-item-flex bordered-bottom">
            <div>
              <div>
                <span @click="expandItem(item)">
                  <c-icon name="expand" class="teams__expand" :class="{ 'active': item.expanded }"></c-icon>
                </span>
                {{ item.name }}
              </div>
              <div class="pl-4 ml-1">
                <small class="text-uppercase text-muted">{{ item.list.length }} франчайзи</small>
              </div>
            </div>
            <div class="text-right">
              <div><small class="text-success text-nowrap" v-price-format="item.income"></small></div>
              <div><small class="text-muted">+</small> <small class="text-success text-nowrap"><span v-price-format="getTotalIncome(item.list)"></span></small></div>
            </div>
          </div>
          <ul class="list-unstyled teams__tree pl-4 ml-1 mt-3 pb-2" v-if="item.expanded">
            <li :data-level="child.level" class="mb-2" v-for="child in item.list">
              <div class="d-flex justify-content-between w-100">
                <div class="h4 m-0">{{ child.name }}</div>
                <div><small class="text-success text-nowrap" v-price-format="child.income"></small></div>
              </div>
            </li>
          </ul>
        </b-container>
      </section>
    </section>

    <router-link :to="{ name: 'TeamsNew' }">
      <c-add-button/>
    </router-link>

    <b-modal v-model="roubleAccountModalVisible" size="sm" hide-header-close hide-header hide-footer centered>
      <div class="pl-3 pt-1">
        <div class="h3 strong mb-3">Рублевый счет</div>
        <div class="mb-4">
          <small class="text-muted">Ваш баланс</small>
          <div class="h2 mb-0"><span v-price-format="fetchedData.totalRub"></span> ₽</div>
        </div>
        <div class="mb-4">
          <b-button variant="primary">Вывести на карту</b-button>
        </div>
      </div>
      <div>
        <b-button variant="link text-info">Отмена</b-button>
      </div>
    </b-modal>

    <b-modal v-model="bncAccountModalVisible" size="sm" hide-header-close hide-header hide-footer centered>
      <div class="pl-3 pt-1">
        <div class="h3 strong mb-3">BnC-счет</div>
        <div class="mb-4">
          <small class="text-muted">Ваш баланс</small>
          <div class="h2 mb-0"><span v-price-format="fetchedData.totalBnc"></span> BnC</div>
        </div>
        <div class="mb-4">
          <div class="h4 mb-2 text-muted">
            BnC можно потратить на покупку нового уровня партнерства
          </div>
          <b-button variant="primary">Купить уровень</b-button>
        </div>
        <div class="mb-4">
          <div class="h4 mb-2 text-muted">
            Или внести предоплату за кластер лицензий
          </div>
          <b-button variant="primary">Внести предоплату</b-button>
        </div>
      </div>
      <div>
        <b-button variant="link text-info">Отмена</b-button>
      </div>
    </b-modal>
  </section>
</template>

<script>
import modelHelper from '../../../mixins/FetchHelper.js';

export default {
  name: 'teams',
  mixins: [modelHelper],
  data () {
    return {
      fetchUrl: '/api/teams',
      pagiUrl: '/api/teams/pagi',
      isLoadDisabled: false,
      fetchedData: {
        totalRub: 0,
        totalBnc: 0,
        intervals: [],
        defaultInterval: 0,
        list: [],
      },
      expandAll: false,
      roubleAccountModalVisible: false,
      bncAccountModalVisible: false,
    }
  },
  computed: {
    pagiParams() {
      return {
        interval: this.fetchedData.defaultInterval,
      };
    },
    infiniteScrollConfig() {
      return {
        callback: this.loadPagination,
        isLoadDisabled: this.isLoadDisabled
      };
    }
  },
  methods: {
    showRoubleAccountModal() {
      this.roubleAccountModalVisible = true;
    },
    showBncAccountModal() {
      this.bncAccountModalVisible = true;
    },
    changeInterval() {
      setTimeout(() => {
        this.loadPagination(true);
        this.expandAll = false;
      }, 100)
    },
    expandItem(item) {
      item.expanded = !item.expanded;
      this.$forceUpdate();
    },
    expandItems() {
      this.expandAll = !this.expandAll;
      this.fetchedData.list.forEach((value) => value.expanded = this.expandAll);
    },
    getTotalIncome(list) {
      return list.reduce(function(prev, curr) {
        return prev + curr.income;
      }, 0);
    }
  },
  mounted() {
   this.loadPagination();
  }
}
</script>

<style lang="scss">
  @import '../../../bootconf';

  .teams {
    .bordered-bottom {
      transition: none !important;
    }

    &__account-item {
      background-color: rgba(255, 255, 255, 0.1);
      color: #fff;
      height: 48px;
      line-height: 48px;
      width: 50%;
      cursor: pointer;

      & + & {
        margin-left: 1px;
      }

      &:first-child {
        border-radius: 2px 0 0 2px;
      }

      &:last-child {
        border-radius: 0 2px 2px 0;
      }
    }

    &__interval {
      position: relative;
      border-bottom: 0;
      padding-top: 0;
      padding-bottom: 0;
      padding-right: 24px;
      color: $text-muted;
      height: 20px !important;
      right: -6px;
    }

    &__expand {
      cursor: pointer;
      transition: .3s transform ease;

      &:not(.active) {
        transform: rotate(-90deg);
      }
    }

    &__tree {
      [data-level] {
        position: relative;

        &::before {
          position: absolute;
          content: '';
          width: 24px;
          height: 24px;
          opacity: 0.7;
          border-radius: 50%;
          color: rgba(0, 0, 0, 0.7);
          overflow: hidden;
          text-align: center;
          font-size: 11px;
          line-height: 24px;
          left: 0;
        }
      }

      [data-level="2"] {
        padding-left: 32px;

        &::before {
          background-color: rgba(29, 182, 50, 0.3);
          content: 'II';
        }
      }

      [data-level="3"] {
        padding-left: 52px;

        &::before {
          background-color: rgba(29, 182, 134, 0.2);
          margin-left: 20px;
          content: 'III';
        }
      }

      [data-level="4"] {
        padding-left: 72px;

        &::before {
          background-color: #e3ecff;
          margin-left: 40px;
          content: 'IV';
        }
      }
    }
  }
</style>


