<template>
  <section class="partner">
    <b-container>
      <c-row-item-block>
        <h1>{{fetchedData.name}}</h1>
        <p class="small text-uppercase text-muted">{{fetchedData.status}}</p>
        <router-link :to="{ name: 'PartnersSendLink', params: { backUrl: getBackUrl() } }">
          <b-button variant="primary" class="partner__link-button" block>Отправить ссылку на подключение</b-button>
        </router-link>
      </c-row-item-block>
      <c-row-item-block>
        <small class="text-muted text-uppercase">Продавец</small>
        <div v-if="!isSellerEdit">
          <span class="afake text-info" @click="toggleIsSellerEdit(true)">{{fetchedData.seller}}</span> <span class="text-muted" v-if="fetchedData.isSellerUser">(вы)</span>
        </div>
        <b-form-group id="fieldset1"
                      label-for="name"
                      :state="undefined"
                      v-if="isSellerEdit"
                      class="partner__form-group">
          <b-form-input id="name"
                        placeholder="Андрей Иванов"
                        :state="undefined"
                        v-model.trim="fetchedData.seller"
                        v-focus
                        @blur.native="toggleIsSellerEdit(false)"></b-form-input>
        </b-form-group>
      </c-row-item-block>
      <c-row-item-block>
        <small class="text-muted text-uppercase">Внедренец</small>
        <div v-if="!isInnovatorEdit">
          <span class="afake text-info" @click="toggleIsInnovatorEdit(true)">{{fetchedData.innovator}}</span> <span class="text-muted" v-if="fetchedData.isInnovatorUser">(вы)</span>
        </div>
        <b-form-group id="fieldset2"
                      label-for="name1"
                      :state="undefined"
                      v-if="isInnovatorEdit"
                      class="partner__form-group">
          <b-form-input id="name1"
                        placeholder="Андрей Иванов"
                        :state="undefined"
                        v-model.trim="fetchedData.innovator"
                        v-focus
                        @blur.native="toggleIsInnovatorEdit(false)"></b-form-input>
        </b-form-group>
      </c-row-item-block>
    </b-container>
    <c-row-item-block class="bg-neutral">
      <b-container>
        <small class="text-uppercase text-muted">Комментарий</small>
        <p class="no-margin">{{fetchedData.comment}}</p>
      </b-container>
    </c-row-item-block>
    <b-container>
      <c-row-item-block>
        <small class="text-muted text-uppercase">Адрес</small>
        <div>{{fetchedData.address}}</div>
      </c-row-item-block>
      <c-row-item-block>
        <small class="text-muted text-uppercase">ФИО контактного лица</small>
        <div>{{fetchedData.fullName}}</div>
      </c-row-item-block>
      <c-row-item-block>
        <small class="text-muted text-uppercase">Должность</small>
        <div>{{fetchedData.position}}</div>
      </c-row-item-block>
      <c-row-item-block>
        <small class="text-muted text-uppercase">Телефон</small>
        <div>{{fetchedData.phone}}</div>
      </c-row-item-block>
      <c-row-item-block>
        <small class="text-muted text-uppercase">Электронная почта</small>
        <div>{{fetchedData.email}}</div>
      </c-row-item-block>
      <c-row-item-block>
        <small class="text-muted text-uppercase">Предполагаемый тариф</small>
        <div>{{fetchedData.estimatedTariff}}</div>
      </c-row-item-block>
    </b-container>
  </section>
</template>

<script>
import modelHelper from '../../../mixins/FetchHelper.js';

export default {
  name: 'partner',
  mixins: [modelHelper],
  data () {
    return {
      fetchUrl: '/api/partners/view',
      isLoadDisabled: false,
      fetchedData: [],
      isSellerEdit: false,
      isInnovatorEdit: false,
    }
  },
  methods: {
    toggleIsSellerEdit(isActive) {
      this.isSellerEdit = isActive;
    },
    toggleIsInnovatorEdit(isActive) {
      this.isInnovatorEdit = isActive;
    },
    getBackUrl() {
      return '/partners/' + this.$route.params.id;
    }
  },
  mounted() {

  }
}
</script>

<style lang="scss">
  .partner {
    &__form-group {
      margin: -6px 0 -7px;
    }
  }

  @media (max-width: 375px) {
    .partner {
      &__link-button {
        font-size: 0.75rem;
      }
    }
  }
</style>


