import Vue from 'vue';
import Router from 'vue-router';

import AuthIndex from '@/components/view/Auth/Index';
import AuthWelcome from '@/components/view/Auth/Welcome';

import PartnersIndex from '@/components/view/Partners/Index';
import PartnersView from '@/components/view/Partners/View';
import PartnersNew from '@/components/view/Partners/New';
import PartnersSendLink from '@/components/view/Partners/SendLink';

import AccountIndex from '@/components/view/Account/Index';
import AccountPayment from '@/components/view/Account/Payment';
import AccountBalance from '@/components/view/Account/Balance';

import TeamsIndex from '@/components/view/Teams/Index';
import TeamsView from '@/components/view/Teams/View';
import TeamsNew from '@/components/view/Teams/New';

import License from '@/components/view/License';
import News from '@/components/view/News';
import Documents from '@/components/view/Documents';

Vue.use(Router);

export default new Router({
  mode: 'history',
  routes: [
    {
        path: '/',
        redirect: '/account/balance'
    },
    {
      path: '*',
      redirect: "/"
    },
    {
      path: '/logout',
      component: null
    },

    {
      path: '/auth',
      name: 'AuthIndex',
      component: AuthIndex
    },
    {
      path: '/auth/welcome',
      name: 'AuthWelcome',
      component: AuthWelcome,
      meta: {
        title: 'Приветствие',
        hasBackAction: true,
        defaultBackPath: '/',
        requiresAuth: true
      }
    },

    {
      path: '/account',
      redirect: '/account/summary',
      meta: {
        requiresAuth: true
      }
    },
    {
      path: '/account/balance',
      name: 'AccountBalance',
      component: AccountBalance,
      meta: {
        title: 'Баланс',
        icon: 'search',
        requiresAuth: true
      }
    },
    {
      path: '/account/summary',
      name: 'AccountIndex',
      component: AccountIndex,
      meta: {
        title: 'Аккаунт',
        icon: 'edit',
        requiresAuth: true
      }
    },
    {
      path: '/account/payment',
      name: 'AccountPayment',
      component: AccountPayment,
      meta: {
        title: 'Оплата аккаунта',
        hasBackAction: true,
        requiresAuth: true
      }
    },


    {
      path: '/partners',
      name: 'PartnersIndex',
      component: PartnersIndex,
      meta: {
        title: 'Партнеры',
        requiresAuth: true
      }
    },
    {
      path: '/partners/view/:id',
      name: 'PartnersView',
      component: PartnersView,
      meta: {
        title: 'Партнер',
        hasBackAction: true,
        requiresAuth: true
      }
    },
    {
      path: '/partners/new',
      name: 'PartnersNew',
      component: PartnersNew,
      meta: {
        title: 'Новый партнер',
        hasBackAction: true,
        requiresAuth: true
      }
    },
    {
      path: '/partners/send-link',
      name: 'PartnersSendLink',
      component: PartnersSendLink,
      meta: {
        title: 'Отправить ссылку',
        hasBackAction: true,
        requiresAuth: true
      }
    },

    {
      path: '/teams',
      name: 'TeamsIndex',
      component: TeamsIndex,
      meta: {
        title: 'Дистрибьюторская сеть',
        requiresAuth: true,
        disabledBg: true
      }
    },

    {
      path: '/teams/view/:id',
      name: 'TeamsView',
      component: TeamsView,
      meta: {
        title: 'Новый франчайзи',
        hasBackAction: true,
        requiresAuth: true
      }
    },
    {
      path: '/teams/new',
      name: 'TeamsNew',
      component: TeamsNew,
      meta: {
        title: 'Новый франчайзи',
        icon: 'search',
        hasBackAction: true,
        requiresAuth: true
      }
    },

    {
      path: '/license',
      name: 'License',
      component: License,
      meta: {
        title: 'Лицензии',
        requiresAuth: true
      }
    },
    {
      path: '/news',
      name: 'News',
      component: News,
      meta: {
        title: 'Новости',
        requiresAuth: true
      }
    },
    {
      path: '/documents',
      name: 'Documents',
      component: Documents,
      meta: {
        title: 'База знаний',
        icon: 'filter',
        requiresAuth: true
      }
    }

  ]
});


