<template>
  <section class="add-partner">
    <div class="row-item-block">
      <b-container>
        <b-form @submit.prevent="submitForm">
          <b-form-group id="fieldset1"
                        label-for="company_name"
                        :state="undefined">
            <b-form-input id="company_name"
                          placeholder="Название компании / лида"
                          required
                          v-model.trim="form.companyName"></b-form-input>
          </b-form-group>

          <b-form-group id="fieldset2"
                        label-for="address"
                        :state="undefined">
            <b-form-input id="address"
                          placeholder="Адрес"
                          required
                          v-model.trim="form.address"></b-form-input>
          </b-form-group>

          <b-form-group id="fieldset3"
                        label-for="person_name"
                        :state="undefined">
            <b-form-input id="person_name"
                          placeholder="ФИО контактного лица"
                          required
                          v-model.trim="form.personName"></b-form-input>
          </b-form-group>

          <b-form-group id="fieldset4"
                        label-for="position"
                        :state="undefined">
            <b-form-input id="position"
                          placeholder="Должность"
                          required
                          v-model.trim="form.position"></b-form-input>
          </b-form-group>

          <b-form-group id="fieldset5"
                        label-for="phone"
                        :state="undefined">
            <b-form-input id="phone"
                          placeholder="Телефон"
                          required
                          v-model.trim="form.phone"></b-form-input>
          </b-form-group>

          <b-form-group id="fieldset6"
                        label-for="email"
                        :state="undefined">
            <b-form-input id="email"
                          placeholder="Электронная почта"
                          required
                          type="email"
                          v-model.trim="form.email"></b-form-input>
          </b-form-group>

          <b-form-group id="fieldset7"
                        label-for="tariff"
                        :state="undefined">
            <b-form-select
                    v-model="form.tariff"
                    :options="tariffsList"
                    v-select-placeholder/>
          </b-form-group>

          <b-form-group>
            <b-form-textarea id="comment"
                             v-model="form.comment"
                             placeholder="Коментарий"
                             :rows="3"
                             :max-rows="6">
            </b-form-textarea>
          </b-form-group>
          <b-button variant="primary" type="submit">Добавить</b-button>

        </b-form>
      </b-container>
    </div>
  </section>
</template>

<script>
import modelHelper from '../../../mixins/FetchHelper.js';

export default {
  name: 'addPartner',
  mixins: [modelHelper],
  data () {
    return {
      form: {
        companyName: '',
        address: '',
        personName: '',
        position: '',
        phone: '',
        email: '',
        tariff: null,
        comment: ''
      },
      requestUrl: '/api/post-success',
      fetchedData: {},
      fetchUrl: '/api/partners/new',
      isLoadDisabled: false
    }
  },
  computed: {
    tariffsList() {
      let list = this.fetchedData.tariffs || [];

      list.unshift({
        value: null,
        text: 'Выберите предполагаемый тариф'
      });

      return list;
    }
  },
  methods: {
    submitForm() {
      let url = this.requestUrl;
      this.axios.post(url, this.form)
        .then(response => {
          this.$router.back();
        })
        .catch(e => {
          this.errors.push(e);
        });
    }
  },
  mounted() {

  }
}
</script>

<style lang="scss">

</style>


