<template>
  <section class="news">
    <b-container>
      <ul class="list-unstyled"
          v-show="fetchedData"
          v-infinite-scroll="infiniteScrollConfig">
        <li class="row-item-block bordered-bottom" v-for="item in fetchedData">
          <div>{{item.title}}</div>
          <small class="text-uppercase text-muted">{{item.date}}</small>
        </li>
      </ul>
    </b-container>
  </section>
</template>

<script>
import modelHelper from '../../mixins/FetchHelper.js';

export default {
  name: 'news',
  mixins: [modelHelper],
  data () {
    return {
      fetchUrl: '/api/news',
      pagiUrl: '/api/news',
      isLoadDisabled: false,
      fetchedData: []
    }
  },
  computed: {
    infiniteScrollConfig() {
      return {
        callback: this.loadPagination,
        isLoadDisabled: this.isLoadDisabled
      };
    }
  }
}
</script>


