<template>
  <section class="team">
    <div class="team__header bg-dark text-inverse bordered-top">
      <div class="row-item-block">
        <b-container>
          <b-row align-v="end" align-h="between">
            <b-col class="col-auto">
              <small>Всего</small>
              <h2 class="no-margin text-primary">{{getTotalIncome()}} {{getTeamName()}}</h2>
            </b-col>
            <b-col class="col-auto"><h2 class="text-muted no-margin">=</h2></b-col>
            <b-col class="col-auto">
              <small>Продажа</small>
              <h2 class="no-margin">{{fetchedData.sale}} {{getTeamName()}}</h2>
            </b-col>
            <b-col class="col-auto"><h2 class="text-muted no-margin">+</h2></b-col>
            <b-col class="col-auto">
              <small>Роялти</small>
              <h2 class="no-margin">{{fetchedData.royalty}} {{getTeamName()}}</h2>
            </b-col>
          </b-row>
        </b-container>
      </div>
    </div>
    <c-line-title>{{fetchedData.count}} участников</c-line-title>
    <b-container>
      <ul class="team__list list-unstyled" v-show="fetchedData.list" v-infinite-scroll="infiniteScrollConfig">
        <li class="team__item text-default bordered-bottom" v-for="item in fetchedData.list">
          <div class="row-item-flex">
            <div>
              <div class="team__icon">
                <c-icon name="person" ></c-icon>
              </div>
              <div>{{item.name}}</div>
              <small class="text-uppercase" :class="{ 'text-muted' : item.is_agent, 'text-danger': !item.is_agent}">{{item.is_agent ? 'Агент' : 'Не аккредитован'}}</small>
            </div>
            <div class="text-right">
              <div class="small" :class="{ 'text-info' : item.franchisees, 'text-muted': !item.franchisees }"><span v-if="item.franchisees">+</span>{{item.franchisees}} {{getTeamName()}}</div>
              <small class="text-muted">{{item.royalty}} франчайзи</small>
            </div>
          </div>
        </li>
      </ul>
    </b-container>
    <router-link :to="{ name: 'TeamsNew', params: {id: getTeamId()} }">
      <c-add-button/>
    </router-link>
  </section>
</template>

<script>
import modelHelper from '../../../mixins/FetchHelper.js';

export default {
  name: 'team',
  mixins: [modelHelper],
  data () {
    return {
      fetchUrl: '/api/teams/view',
      pagiUrl: '/api/teams/pagi',
      isLoadDisabled: false,
      fetchedData: {
        sale: 0,
        royalty: 0,
        list: []
      }
    }
  },
  computed: {
    infiniteScrollConfig() {
      return {
        callback: this.loadPagination,
        isLoadDisabled: this.isLoadDisabled
      };
    }
  },
  methods: {
    fetchData(done) {
      const url =  `${this.fetchUrl}/${this.$route.params.id}`;
      if (!url) return;

      let cached = this.$ls.get(url);
      if (cached) {
        this.fetchedData = cached;
      }

      this.isLoadDisabled = true;

      this.axios.get(url)
        .then(response => {
          if (typeof done === 'function') done();
          this.isLoadDisabled = false;

          if (JSON.stringify(response.data) === JSON.stringify(this.fetchedData)) return;

          this.fetchedData = response.data;
          this.$ls.set(url, response.data);
        })
        .catch(e => {
          if (typeof done === 'function') done();

          this.errors.push(e);
          this.isLoadDisabled = false;
        });
    },
    loadPagination() {
      const url = `${this.pagiUrl}/${this.$route.params.id}`;
      if (!url) return;

      this.isLoadDisabled = true;
      this.axios.get(url)
        .then(response => {
          response.data.forEach((item) => {
            this.fetchedData.list.push(item);
          });
          this.isLoadDisabled = false;
        })
        .catch(e => {
          this.errors.push(e);
          this.isLoadDisabled = false;
        });
    },
    configureHeader() {
        this.$route.meta.title = `Команда ${this.getTeamName()}`;
    },
    getTotalIncome() {
      return this.fetchedData.sale + this.fetchedData.royalty;
    },
    getTeamName() {
      return `${String(this.$route.params.id).toUpperCase()}`;
    },
    getTeamId() {
      return this.$route.params.id;
    }
  },
  mounted() {
      this.configureHeader();
  }
}
</script>

<style lang="scss">
  @import '../../../bootconf';

  .team {
    &__header {
      .container {
        max-width: 420px;
      }

      .col:nth-child(even) {
        padding: 0;
      }
    }

    &__list {
      padding-left: 50px !important;

     &.list-hover {
       li {
         &::before {
           left: -90px;
         }
       }
     }
    }

    &__item {
      position: relative;

      &:last-child {
        .team__icon::after {
          display: none;
        }
      }
    }

    &__icon {
      position: absolute;
      left: -50px;
      top: 50%;
      transform: translateY(-50%);

      &::after {
        position: absolute;
        content: '';
        width: 1.5px;
        height: 62px;
        top: 20px;
        left: 50%;
        transform: translateX(-50%);
        background: $success;
      }
    }
  }
</style>


