<template>
  <section class="documents">
    <b-container>
      <ul class="list-unstyled list-hover" v-show="fetchedData">
        <li class="documents__item bordered-bottom" v-for="document in fetchedData">
          <a :href="document.url" class="row-item-flex" target="_blank">
            <c-icon name="document" class="documents__icon"></c-icon>
            <div>{{document.name}}</div>
          </a>
        </li>
      </ul>
    </b-container>
  </section>
</template>

<script>
import modelHelper from '../../mixins/FetchHelper.js';

export default {
  name: 'documents',
  mixins: [modelHelper],
  data () {
    return {
      fetchUrl: '/api/documents',
      pagiUrl: '/api/documents',
      isLoadDisabled: false,
      fetchedData: []
    }
  }
}
</script>

<style lang="scss">
  .documents {
    &__item {
      .row-item-flex {
        min-height: 87px;
        justify-content: flex-start;
      }
    }

    &__icon {
      min-width: 24px;
      margin-right: 15px;
    }
  }
</style>


