<template>
  <section class="partners">
    <div class="partners__header bg-dark text-inverse bordered-top">
      <div class="row-item-block">
        <b-container>
          <b-row align-v="end" align-h="between">
            <b-col class="col-auto">
              <small class="d-inline-block">Доход<br>от&nbsp;партнеров</small>
              <h2 class="no-margin">{{fetchedData.incoming}}$</h2>
            </b-col>
            <b-col class="col-auto">
              <small class="d-inline-block">Лицензий</small>
              <h2 class="no-margin">{{fetchedData.license}}</h2>
            </b-col>
            <b-col class="col-auto">
              <small class="d-inline-block">Потенциальных<br>сделок&nbsp;на</small>
              <h2 class="no-margin text-primary">{{fetchedData.deals}}$</h2>
            </b-col>
          </b-row>
        </b-container>
      </div>
    </div>
    <b-container>
      <ul class="list-unstyled list-hover" v-show="fetchedData.list" v-infinite-scroll="infiniteScrollConfig">
        <li class="bordered-bottom" v-for="item in fetchedData.list">
          <router-link :to="{ name: 'PartnersView', params: { id: item.id} }" class="row-item-block">
            <div>{{item.name}}</div>
            <small class="text-uppercase text-muted">{{item.type}}</small>
          </router-link>
        </li>
      </ul>
    </b-container>
    <router-link :to="{ name: 'PartnersNew' }">
      <c-add-button/>
    </router-link>
  </section>
</template>

<script>
import modelHelper from '../../../mixins/FetchHelper.js';

export default {
  name: 'partners',
  mixins: [modelHelper],
  data () {
    return {
      fetchUrl: '/api/partners',
      pagiUrl: '/api/partners/pagi',
      isLoadDisabled: false,
      fetchedData: {}
    }
  },
  computed: {
    infiniteScrollConfig() {
      return {
        callback: this.loadPagination,
        isLoadDisabled: this.isLoadDisabled
      };
    }
  },
  methods: {

  },
  mounted() {

  }
}
</script>

<style lang="scss">
  .partners {
    &__header {
      .container {
        max-width: 420px;
      }

      .row {
        flex-wrap: inherit;
      }
    }
  }
</style>


