<template>
  <section class="account">
    <div class="bg-dark">
      <b-container>
        <div class="row-item-flex">
          <h3 class="text-inverse no-margin">{{fetchedData.userName}}</h3>
          <div class="text-success">{{fetchedData.position}}</div>
        </div>
      </b-container>
    </div>
    <section>
      <b-container>
        <div class="row-item-flex bordered-bottom">
          <div>
            <div>{{fetchedData.registrationDate}}</div>
            <small class="text-uppercase text-muted">Дата регистрации</small>
          </div>
          <div class="text-right">
            <div>{{fetchedData.id}}</div>
            <small class="text-muted">id</small>
          </div>
        </div>
        <div class="row-item-block">
          <div>{{fetchedData.phone}}</div>
          <small class="text-uppercase text-muted">Телефон</small>
        </div>
      </b-container>
    </section>
    <section>
      <c-line-title>Оплата аккаунта</c-line-title>
      <b-container>
        <div class="row-item-block bordered-bottom">
          <div>Абонентская плата {{fetchedData.subscriptionFee}}₽</div>
          <small class="text-uppercase text-muted" v-if="fetchedData.isSubscriptionFeePaid">Оплачено</small>
        </div>
        <div class="row-item-block">
          <div>{{fetchedData.nextSubscriptionFeeData}}</div>
          <small class="text-uppercase text-muted">Следующий платеж</small>
        </div>
      </b-container>
    </section>
    <b-container>
      <div class="row-item-block bordered-top">
        <b-row align-v="center">
          <b-col class="col-auto">
            <router-link :to="{ name: 'AccountPayment' }">
              <b-button variant="primary">Оплатить</b-button>
            </router-link>
          </b-col>
          <b-col class="col-auto">
            <router-link to="/" class="text-info h4 no-margin">
              Редактировать<br>
              платежные данные
            </router-link>
          </b-col>
        </b-row>
      </div>
      <div class="row-item-block">
        <p class="no-margin">{{fetchedData.positionDesc}}</p>
      </div>
    </b-container>
  </section>
</template>

<script>
import modelHelper from '../../../mixins/FetchHelper.js';

export default {
  name: 'account',
  mixins: [modelHelper],
  data () {
    return {
      fetchUrl: '/api/account/index',
      isLoadDisabled: false,
      fetchedData: []
    }
  },
  methods: {

  },
  mounted() {

  }
}
</script>

<style lang="scss">

</style>


