<template>
  <section class="license">
    <b-container class="bg-dark">
      <div class="row-item-block">
        <h3 class="text-inverse no-margin">Куплено лицензий: {{fetchedData.licenseCount}}</h3>
      </div>
    </b-container>
    <b-container>
      <div class="row-item-block">
        <p class="no-margin">Покупайте лицензии и привлекайте новых партнеров в систему с большей выгодой.</p>
      </div>
      <b-form @submit.prevent="submitForm">
        <b-form-group id="fieldset1"
                      label-for="select"
                      :state="undefined">
          <b-form-select
                  v-model="form.license"
                  :options="licenseList"
                  v-select-placeholder/>
        </b-form-group>
        <b-button type="submit" variant="primary">Купить</b-button>
      </b-form>
    </b-container>
  </section>
</template>

<script>
import modelHelper from '../../mixins/FetchHelper.js';

export default {
  name: 'license',
  mixins: [modelHelper],
  data () {
    return {
      form: {
        license: null
      },
      requestUrl: '/api/post-success',
      fetchUrl: '/api/license',
      isLoadDisabled: false,
      fetchedData: {},
    }
  },
  methods: {
    submitForm() {
      let url = this.requestUrl;
      this.axios.post(url, this.form)
        .then(response => {
          this.form.license = null;
        })
        .catch(e => {
          this.errors.push(e);
        });
    }
  },
  computed: {
    licenseList() {
      let list = this.fetchedData.licenseList || [];

      list.unshift({
        value: null,
        text: 'Выберите лицензию'
      });

      return list;
    }
  },
  mounted() {

  }
}
</script>


