<template>
  <section class="balance">
    <div class="bg-dark bordered-top">
      <b-container>
        <div class="row-item-flex">
          <div class="text-primary">{{fetchedData.incoming}}$</div>
          <b-button variant="primary">Вывести</b-button>
        </div>
      </b-container>
    </div>
    <b-alert :show="!fetchedData.isSubscriptionFeePaid" variant="danger">
      <b-container>
        <small>Вы не оплатили абонентскую плату :(</small>
        <br>
        <router-link to="payment" class="text-inverse">Оплатить</router-link>
      </b-container>
    </b-alert>
    <section>
      <c-line-title>Расчёт</c-line-title>
      <b-container>
        <ul class="list-unstyled">
          <li v-for="item in fetchedData.calculation" class="row-item-flex" data-align="bottom">
            <div>
              <small class="text-muted text-uppercase">{{item.position}}</small>
              <div>{{item.operation}}</div>
            </div>
            <div class="text-info">{{item.cost}}$</div>
          </li>
        </ul>
      </b-container>
    </section>
    <section>
      <c-line-title>Правила расчета</c-line-title>
      <b-container>
        <div class="row-item-block" v-for="item in fetchedData.calculationRules">
          <small class="text-muted text-uppercase">{{item.position}}</small>
          <p>{{item.desc}}</p>
        </div>
      </b-container>
    </section>
  </section>
</template>

<script>
import modelHelper from '../../../mixins/FetchHelper.js';

export default {
  name: 'balance',
  mixins: [modelHelper],
  data () {
    return {
      fetchUrl: '/api/account/balance',
      isLoadDisabled: false,
      fetchedData: []
    }
  },
  methods: {

  },
  mounted() {

  }
}
</script>


