<template>
  <section class="add-member">
    <b-tabs nav-wrapper-class="bg-dark container text-uppercase">
      <b-tab title="Команда A" :active="isTabActive('a')">
        <div class="row-item-block">
          <b-container>
            <b-form @submit.prevent="submitForm">
              <b-form-group id="fieldset1"
                            label-for="firstName"
                            :state="undefined">
                <b-form-input id="firstName"
                              placeholder="Имя"
                              required
                              v-model.trim="form.firstName"></b-form-input>
              </b-form-group>

              <b-form-group id="fieldset2"
                            label-for="lastName"
                            :state="undefined">
                <b-form-input id="lastName"
                              placeholder="Фамилия"
                              required
                              v-model.trim="form.lastName"></b-form-input>
              </b-form-group>

              <b-form-group id="fieldset3"
                            label-for="email"
                            :state="undefined">
                <b-form-input id="email"
                              placeholder="Электронная почта"
                              required
                              type="email"
                              v-model.trim="form.email"></b-form-input>
              </b-form-group>

              <b-form-group id="fieldset4"
                            label-for="phone"
                            :state="undefined">
                <b-form-input id="phone"
                              placeholder="Номер телефона"
                              required
                              v-model.trim="form.phone"></b-form-input>
              </b-form-group>

              <b-button variant="primary" type="submit">Добавить</b-button>

            </b-form>
          </b-container>
        </div>
      </b-tab>
      <b-tab title="Команда B" :active="isTabActive('b')">
        <div class="row-item-block">
              <b-container>
                  <b-form>
                      <b-form-group id="fieldset1"
                                    label-for="firstName"
                                    :state="undefined">
                          <b-form-input id="firstName"
                                        placeholder="Имя"
                                        required
                                        v-model.trim="form.firstName"></b-form-input>
                      </b-form-group>

                      <b-form-group id="fieldset2"
                                    label-for="lastName"
                                    :state="undefined">
                          <b-form-input id="lastName"
                                        placeholder="Фамилия"
                                        required
                                        v-model.trim="form.lastName"></b-form-input>
                      </b-form-group>

                      <b-form-group id="fieldset3"
                                    label-for="email"
                                    :state="undefined">
                          <b-form-input id="email"
                                        placeholder="Электронная почта"
                                        required
                                        type="email"
                                        v-model.trim="form.email"></b-form-input>
                      </b-form-group>

                      <b-form-group id="fieldset4"
                                    label-for="phone"
                                    :state="undefined">
                          <b-form-input id="phone"
                                        placeholder="Номер телефона"
                                        required
                                        v-model.trim="form.phone"></b-form-input>
                      </b-form-group>

                      <b-button variant="primary" type="submit">Добавить</b-button>

                  </b-form>
              </b-container>
          </div>
      </b-tab>
    </b-tabs>
  </section>
</template>

<script>

export default {
  name: 'AddMember',
  data () {
    return {
      requestUrl: '/api/post-success',
      form: {
        firstName: '',
        lastName: '',
        email: '',
        phone: ''
      }
    }
  },
  methods: {
    isTabActive(teamId) {
      return this.$route.params.id === teamId;
    },
    submitForm() {
      let url = this.requestUrl;
      this.axios.post(url, this.form)
        .then(response => {
            this.$router.back();
          })
        .catch(e => {
          this.errors.push(e);
        });
    }
  },
  mounted() {
  }
}
</script>

<style lang="scss">
  .add-member {
    .nav-tabs {
      max-width: 767px;
    }
  }
</style>


