<template>
  <section class="payment">
    <div class="bg-dark">
      <div class="row-item-block payment__heading text-inverse">
        <b-container>
          <b-row align-v="end" align-h="between">
            <b-col class="col-auto">
              <small class="d-inline-block">Сумма<br>к&nbsp;оплате</small>
              <h2 class="no-margin">{{fetchedData.subscriptionFee}}₽</h2>
            </b-col>
            <b-col class="col-auto">
              <small class="d-inline-block">Дата окончания<br>тарифа</small>
              <h2 class="no-margin text-primary">{{fetchedData.subscriptionEndsDate}}</h2>
            </b-col>
          </b-row>
        </b-container>
      </div>
    </div>
    <b-container>
      <c-row-item-block>
        <b-form @submit.prevent="submitForm">
          <b-form-group id="fieldset1"
                        label-for="card"
                        :state="undefined">
            <b-form-input id="card"
                          placeholder="Номер карты"
                          required
                          v-model.trim="form.number"></b-form-input>
          </b-form-group>
          <b-form-group id="fieldset2"
                        label-for="name"
                        :state="undefined">
            <b-form-input id="name"
                          placeholder="Имя владельца"
                          required
                          v-model.trim="form.name"></b-form-input>
          </b-form-group>
          <b-row>
            <b-col>
              <b-form-group id="fieldset3"
                            label-for="date"
                            :state="undefined">
                <b-form-input id="date"
                              placeholder="Срок карты"
                              required
                              v-model.trim="form.date"></b-form-input>
              </b-form-group>
            </b-col>
            <b-col>
              <b-form-group id="fieldset4"
                            label-for="csv"
                            :state="undefined">
                <b-form-input id="csv"
                              placeholder="CSV"
                              required
                              v-model.trim="form.csv"></b-form-input>
              </b-form-group>
            </b-col>
          </b-row>
          <c-row-item-block>
            <b-form-checkbox id="checkbox1"
                             v-model="form.isSave"
                             value="accepted"
                             unchecked-value="not_accepted">
              Сохранить карту
            </b-form-checkbox>
          </c-row-item-block>
          <c-row-item-block>
            <b-button type="submit" variant="primary">Оплатить</b-button>
          </c-row-item-block>
        </b-form>
      </c-row-item-block>
    </b-container>
  </section>
</template>

<script>
import modelHelper from '../../../mixins/FetchHelper.js';

export default {
  name: 'payment',
  mixins: [modelHelper],
  data () {
   return {
    form: {
      number: '',
      name: '',
      date: '',
      csv: '',
      isSave: ''
     },
     requestUrl: '/api/post-success',
     fetchUrl: '/api/account/payment',
     isLoadDisabled: false,
     fetchedData: []
    }
  },
  methods: {
    submitForm() {
      let url = this.requestUrl;
      this.axios.post(url, this.form)
        .then(response => {
          this.$router.back();
        })
        .catch(e => {
          this.errors.push(e);
        });
    }
  },
  mounted() {

  }
}
</script>

<style lang="scss">
  .payment {
      &__heading {
        max-width: 320px;
      }
  }
</style>


